import { useState, useEffect } from "react"
import TrackVisibility from 'react-on-screen'
import { Container, Row, Col } from "react-bootstrap"
import profileImg from "../assets/img/profile.gif"
import { ArrowRightCircle } from 'react-bootstrap-icons'

const Banner = (props) => {
	const { t } = props
    const [loopNum, setLoopNum]         = useState(0)
    const [isDeleting, setIsDeleting]   = useState(false)
    const [text, setText]   = useState('')
    const [delta, setDelta] = useState(300 - Math.random() * 100)
    const [index, setIndex] = useState(1)
    const toRotate  = ["Web Developer", "Web Designer", "UI/UX Designer"]
    const period    = 2000

    useEffect(() => {
        let ticker = setInterval(() => {
            tick()
        }, delta)

        return () => { clearInterval(ticker) }
    }, [text])

    const tick = () => {
        let i = loopNum % toRotate.length
        let fullText = toRotate[i]
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1)

        setText(updatedText)

        if (isDeleting) {
            setDelta(prevDelta => prevDelta / 2)
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true)
            setIndex(prevIndex => prevIndex - 1)
            setDelta(period)
        } else if (isDeleting && updatedText === '') {
            setIsDeleting(false)
            setLoopNum(loopNum + 1)
            setIndex(1)
            setDelta(500)
        } else {
            setIndex(prevIndex => prevIndex + 1)
        }
    }

    return (
        <section className="banner" id="home">
            <Container>
                <Row className="aligh-items-center ">
                    <Col xs={12} md={6} xl={7}>
                        <TrackVisibility>
                            {({ isVisible }) =>
                                <div className={isVisible ? "animate__animated animate__fadeIn position-absolute" : "position-absolute"}>
                                     <Row className="">
                                        <Col xs={12} md={6}>
                                            <span className="tagline">{t('lng_hello')}</span>
                                            <h1>{t('lng_hi_name')} <span className="txt-rotate" data-rotate='[ "Web Developer", "Web Designer", "UI/UX Designer" ]'><span className="wrap">{text}</span></span></h1>
                                            <p>
                                                {t('lng_title_des')}👋!<br/>
                                                {t('lng_describe')}
                                            </p>
                                            <button onClick={() => console.log('connect')}>{t('lng_connect')} <ArrowRightCircle size={25} /></button>
                                        </Col>
                                    </Row>
                                </div>}
                        </TrackVisibility>
                    </Col>
                    <Col xs={12} md={6} xl={5}>
                        <div>
                            <img src={profileImg} alt="Header Img" />
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default Banner